import { useEffect, useState } from "react";
import {get} from '../helpers/httpClient'
export const Ruta1 = ()=>{

    const [movie, setMovie] = useState(''); 

    useEffect(()=>{
        get('/movie/'+566525).then(data => {
            setMovie(data); 
        })
    },[])

    return(
        <div>
            <img src={'https://image.tmdb.org/t/p/w300' + movie.poster_path}></img>
            <p>{movie.title}</p>
        </div>
    )
}